<template>
    <div class="background">
        <p>cliquez pour mofifier vos informations:</p>
        <form class="form">
            <div class="row">
            <div>
                <label for="name" aria-label="Votre nom">Nom*</label>
                <input  v-model="lastname" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>

            <div>
                <label for="firstname" aria-label="Votre Prénom">Prénom*</label>
                <input v-model="firstname" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>
        </div>

        <div class="row ">
            <div>
                <label for="phone" aria-label="Votre numéro de téléphone">Téléphone*</label>
                <input v-model="phone" type="number" pattern="[ 0-9\p{L}]{15}" required>
            </div>

            <div>
                <label for="mail" aria-label="Votre adresse email" >Mail*</label>
                <input v-model="mail" type="email" required>
            </div>
        </div>

        <div class="row ">
            <div>
                <label for="adress" aria-label="Votre adresse">Adresse</label>
                <input v-model="adress" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" >
            </div>

            <div>
                <label for="date" aria-label="Votre date de naisssance" >Date de naissance*</label>
                <input v-model="date" type="date" >
            </div>
        </div>

        <div class="row">
            <div>
                <label for="password" aria-label="Entrer le mot de passe">Nouveau mot de passe*</label>
                <input v-model="password" type="password" pattern="[ A-Za-z-0-9\p{L}]{2,254}" >
            </div>
                
            <div>
                <label for="password" aria-label="Entrer le même mot de passe">Confirmer le mot de passe*</label>
                <input v-model="password" type="password" pattern="[ A-Za-z-0-9\p{L}]{2,254}" >
            </div>

        </div>

                <button class="btn">modifier mes informations</button>

                
        </form>
    </div>
</template>

<script>
import {getUserById} from '../api/users.api'
//import {updateUserById} from '../api/users.api'

export default ({
    name: 'Clientinfo',
    data(){
        return{
            lastname:"",
            firstname:"",
            phone:"",
            mail:"",
            adress:"",
            date:""

        }
    },
    methods:{
        getUserInfo(){
            
            getUserById(this.$store.state.userId, this.$store.state.token)
            .then(response=>{
                            console.log("la rep",response)

                            let user = {
                                lastname: response.data.lastname,
                                firstname: response.data.firstname,
                                telephone: response.data.tel,
                                email: response.data.email,
                                adress: response.data.adress,
                                dateOfBirth: response.data.dateOfBirth
                            }
                            

                            localStorage.setItem("user", JSON.stringify(user))
                            this.lastname = user.lastname
                            this.firstname = user.firstname
                            this.phone = user.telephone
                            this.mail = user.email
            })
            .catch(error=>{
            console.log(error)
             })
        }
    },
    mounted(){

        this.getUserInfo()
        
         

     }
})

</script>

