<template>
  <section class="Customer">
    <div class="Customer__box">
         
         <div class="admin" v-if="admin">
           <h2>Bonjour cher collaborateur!</h2>
            <div class="row">

                  <div class="article">
                    <h4>Ajouter un article</h4>
                    <div class="loader" v-if="loading" >Loading...</div>
                    
                    <form v-if="!loading" method="POST"  enctype="multipart/form-data">
                      <div class="column">
                        <label for="avatar">Une petit photo 🤳🏽:</label>
                        <input @change="download" type="file" id="image" name="image" accept="image/*">
                        <img id="preview" class="downloaded_pic">
                        <p>{{ this.aisle }} </p>
                      </div>

                      <div>
                          <label for="title" aria-label="Le nom du produit">Titre</label>
                          <input  v-model="title" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
                      </div>

                      <div>
                          <label for="description" aria-label="Description du produit">Description</label>
                          <input  v-model="description" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
                      </div>

                       <div>
                          <label for="price" aria-label="Le prix du produit">Prix</label>
                          <input  v-model="price" type="Number" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
                      </div>

                      <label for="aisle-select">Rayon:</label>

                      <select v-model="aisle" name="aisle" id="pet-select">
                          <option value="">--Choisissez une option--</option>
                          <option value="Maternité">Maternité</option>
                          <option value="Les essentiels">Les essentiels</option>
                          <option value="Snack">Snack</option>
                          <option value="Cadeaux">Cadeaux</option>
                         
                      </select>

                      <div>
                          <label for="quantity" aria-label="Quantité du produit">Quantité</label>
                          <input  v-model="quantity" type="Number" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
                      </div>

                              <button type="submit" @click="createArt"  class="center space">Envoyer</button>


                    </form>
                  </div>

                  <div class="article">
                    <h4>Les commandes en cours</h4>
                  </div>
            </div>

        </div>
          

        <div class="client" v-if="connectedClient">
          <h3>Mes informations</h3>

          <clientinfo/>

          <h3>Historique de mes commandes</h3>
        </div> 

         <div v-if="connexion_box" class="Customer__box__Connexion">
           <div class="link">
              <router-link to="/client/login"> <h2>Connectez-vous</h2></router-link>
              <p class="margin_around">ou</p>
              <router-link to="/client/signup">  <h2>Inscrivez-vous</h2></router-link> 
           </div>
            <router-view/>
         </div>

    </div>

  </section>
</template>
<script>
// @ is an alias to /src
import {createArticle} from '../api/articles.api'
import { mapState } from 'vuex'
import clientinfo from '../components/clientinfo.vue'


export default {
  components: { clientinfo },
  name: 'Client',
  data(){
    return{
        title: "", 
        description:"",
        price:"",
        aisle:"",
        quantity: Number,
        imageUrl:"",
        admin: false,
        connectedClient: false,
        connexion_box:true,
        loading:false
    }
  },
   computed: {
    ...mapState(['token'])
    },
  methods:{
    download(event) {
      var preview = document.getElementById('preview');
      preview.src = URL.createObjectURL(event.target.files[0]);
      preview.onload = function() {
        URL.revokeObjectURL(preview.src) // free memory
      }
      this.imageUrl = event.target.files[0];
       console.log(event.target.files)
    },
    
    createArt(e){
      e.preventDefault();
      this.loading=true
      
      

      createArticle(this.title, this.description, this.token , this.imageUrl, this.price, this.aisle, this.quantity)
      .then(response=>{
                    console.log(response)
                    this.title=""
                    this.description=""
                    this.price=""
                    this.aisle=""
                    this.quantity=Number
                     this.loading=false
      })
      .catch(error=>{
                    console.log(error)
                })
    }
  },
  mounted(){
    if(this.$store.state.admin == null){
      this.connexion_box = true
       this.$router.push({path:'/client/login'})
    }
    else if(this.$store.state.admin == false){
      console.log(this.$store.state, this.$store.state.admin)
      this.connectedClient = true,
      this.connexion_box = false
    }else{
     console.log(this.$store.state, this.$store.state.admin)
          this.connexion_box = false
          this.admin = true

    }

  }
}
</script>
