<template>
    <section class="section">
        <div class="basket">
            <h3>Mon panier: </h3>
            <div v-for="article in basketArray" :key="article.id">
              <div  class="articles_list"> <p>{{article.title}} </p> <p class="bold">{{ article.price}}€ <span @click="deleteFromBasket(article.id)" class="remove" >retirer</span> </p></div> 
            </div>
            <p class="bold">Total: {{total}}€</p>

        </div>
        
        <form class="command">
            <h3>Mes informations</h3>

            <div class="row">
            <div>
                <label for="name" aria-label="Votre nom">Nom*</label>
                <input  v-model="lastname" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>

            <div>
                <label for="firstname" aria-label="Votre Prénom">Prénom*</label>
                <input v-model="firstname" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>
        </div>

        <div class="row ">
            <div>
                <label for="phone" aria-label="Votre numéro de téléphone">Téléphone*</label>
                <input v-model="phone" type="number" pattern="[ 0-9\p{L}]{15}" required>
            </div>

            <div>
                <label for="mail" aria-label="Votre adresse email" >Mail*</label>
                <input v-model="mail" type="email" required>
            </div>
        </div>

        <div class="row">
            <div>
                <label for="patient" aria-label="Entrer le nom du patient">Nom du patient (si différent)</label>
                <input  v-model="patient" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" >
            </div>

            <div>
                <label for="room" aria-label="Entrer le numéro de chambre">Entrer le numéro de chambre</label>
                <input v-model="room" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" >
            </div>

            <div>
                <label for="service" aria-label="Entrer le nom du service">Entrer le nom du service*</label>
                <input v-model="service" type="text" pattern="[ A-Za-z-0-9\p{L}]{2,254}" required>
            </div>
        </div>

                <button class="btn">Valider mon panier</button>

                 <div>
            <p>ou</p>
            <h4 @click="connexion" class="router_for_connexion">Connexion & inscription</h4>
        </div>
        </form>

       
    </section>
</template>
<script>
import {getUserById} from '../api/users.api'

export default ({
    data() {
       return{
           basketArray: [],
           total: 0,
           lastname:"",
           firstname:"",
           phone:"",
           mail:""
       } 
    },
    methods:{
        connexion(){
             this.$router.push({path:'/client/login'})
        },
        deleteFromBasket(id){
          
            const basket = JSON.parse(localStorage.getItem("basket"));

            function deletedOne(article) {
            return article.id === `${id}`;
            }

            console.log(basket,deletedOne );

             basket.splice(basket.findIndex(deletedOne), 1)
            
            localStorage.setItem("basket", JSON.stringify(basket))

            const newBasket = JSON.parse(localStorage.getItem("basket"));

            this.basketArray = newBasket

             let prices = newBasket.map(x => Number(x.price))
             console.log("ici", prices.length)
            if(prices.length == 0){
                this.total = 0
            }else{
            this.total = prices.reduce((price, totalprice) => price + totalprice)
            }
         

        },
         getUserInfo(){
            getUserById(this.$store.state.userId, this.$store.state.token)
            .then(response=>{
                            console.log("la rep",response)

                            let user = {
                                lastname: response.data.lastname,
                                firstname: response.data.firstname,
                                telephone: response.data.tel,
                                email: response.data.email

                            }
                            

                            localStorage.setItem("user", JSON.stringify(user))
                            this.lastname = user.lastname
                            this.firstname = user.firstname
                            this.phone = user.telephone
                            this.mail = user.email
            })
            .catch(error=>{
            console.log(error)
             })
        },
    },
   
    mounted(){
                this.getUserInfo()
            let basket = JSON.parse(localStorage.getItem("basket"));

            basket.forEach(article => {
                            this.basketArray.push(article)

            });

            let prices = basket.map(x => Number(x.price))

            this.total = prices.reduce((price, totalprice) => price + totalprice)
                                


       
    }
})
</script>

